    } 
}
.sub-btn {
    color: #050505;
    border-style: none;
    border-radius: 8px;
    background: #9BB537;
    padding: 8px 24px 8px 24px;
    width: 165px;
    font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: 0.04em;
  
//    display: flex;
//   align-items: center; 
//   text-align: center;

  transition-property: background, cursor;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  
}
.sub-btn:is(:hover, :focus) {
    background: rgba(155, 181, 55, 0.7);
    // cursor: pointer;
  }

.sub-form {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 10px;
    margin-top: 15px;
    input {
        color: rgba(5, 5, 5, 0.50);
        font-family: Inter;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 1.25; /* 125% */

        padding-left: 16px;
        height: 40px;
        width: 264px;
        margin-right: 24px;
        background-color: var(--primary-text-color);
        border-radius: 15px;
        border: 1px solid rgba(5, 5, 5, 0.20);
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
        color: rgb(0, 0, 0);
    // border-radius: 4px;
    }
}

